import {
  cilFlagAlt,
  cilHome,
  cilLockLocked,
  cilPhone,
  cilUser,
} from '@coreui/icons';
import CIcon from '@coreui/icons-react';
import {
  CButton,
  CCard,
  CCardBody,
  CCol,
  CContainer,
  CForm,
  CFormInput,
  CInputGroup,
  CInputGroupText,
  CRow,
} from '@coreui/react';
import axios from 'axios';
import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { api } from 'src/views/api';

const Register = () => {
  const [data, setData] = useState({
    email: '',
    firstName: '',
    lastName: '',
    countryCode: '',
    phoneNumber: '',
    password: '',
    storeName: '',
    storeAddress: '',
    storeDescription: '',
    role: 'admin',
  });
  const { push } = useHistory();

  const persianNumbers = [
      /۰/g,
      /۱/g,
      /۲/g,
      /۳/g,
      /۴/g,
      /۵/g,
      /۶/g,
      /۷/g,
      /۸/g,
      /۹/g,
    ],
    arabicNumbers = [
      /٠/g,
      /١/g,
      /٢/g,
      /٣/g,
      /٤/g,
      /٥/g,
      /٦/g,
      /٧/g,
      /٨/g,
      /٩/g,
    ],
    fixNumbers = function (str) {
      if (typeof str === 'string') {
        for (var i = 0; i < 10; i++) {
          str = str.replace(persianNumbers[i], i).replace(arabicNumbers[i], i);
        }
      }
      return str;
    };

  const handleChange = (e) => {
    setData({
      ...data,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      await axios.post(`${api}/api/user/register`, {
        email: data?.email,
        firstName: data?.firstName,
        lastName: data?.lastName,
        countryCode: fixNumbers(data?.countryCode),
        phoneNumber: fixNumbers(data?.phoneNumber),
        password: data?.password,
        store: {
          name: data?.storeName,
          store: data.storeAddress,
          description: data?.storeDescription,
        },
        role: 'admin',
      });

      push('/');
    } catch (error) {
      alert(error?.response?.message);
    }
  };
  return (
    <div className='bg-light min-vh-100 d-flex flex-row align-items-center'>
      <CContainer>
        <CRow className='justify-content-center'>
          <CCol md={9} lg={7} xl={6}>
            <CCard className='mx-4'>
              <CCardBody className='p-4'>
                <CForm>
                  <h1>ثبت نام</h1>
                  <CInputGroup className='mb-3'>
                    <CInputGroupText>
                      <CIcon icon={cilUser} />
                    </CInputGroupText>
                    <CFormInput
                      placeholder='نام'
                      autoComplete='firstName'
                      name='firstName'
                      onChange={handleChange}
                    />
                  </CInputGroup>
                  <CInputGroup className='mb-3'>
                    <CInputGroupText>
                      <CIcon icon={cilUser} />
                    </CInputGroupText>
                    <CFormInput
                      placeholder='نام خانوادگی'
                      autoComplete='lastName'
                      name='lastName'
                      onChange={handleChange}
                    />
                  </CInputGroup>
                  <CInputGroup className='mb-3'>
                    <CInputGroupText>
                      <CIcon icon={cilFlagAlt} />
                    </CInputGroupText>
                    <CFormInput
                      placeholder='کد کشور'
                      name='countryCode'
                      onChange={handleChange}
                    />
                  </CInputGroup>
                  <CInputGroup className='mb-3'>
                    <CInputGroupText>
                      <CIcon icon={cilPhone} />
                    </CInputGroupText>
                    <CFormInput
                      placeholder='شماره همراه'
                      name='phoneNumber'
                      onChange={handleChange}
                    />
                  </CInputGroup>
                  <CInputGroup className='mb-3'>
                    <CInputGroupText>
                      <CIcon icon={cilHome} />
                    </CInputGroupText>
                    <CFormInput
                      placeholder='نام فروشگاه'
                      name='storeName'
                      onChange={handleChange}
                    />
                  </CInputGroup>
                  <CInputGroup className='mb-3'>
                    <CInputGroupText>
                      <CIcon icon={cilHome} />
                    </CInputGroupText>
                    <CFormInput
                      placeholder='آدرس فروشگاه'
                      name='storeAddress'
                      onChange={handleChange}
                    />
                  </CInputGroup>
                  <CInputGroup className='mb-3'>
                    <CInputGroupText>
                      <CIcon icon={cilHome} />
                    </CInputGroupText>
                    <CFormInput
                      placeholder='توضیحات فروشگاه'
                      name='storeDescription'
                      onChange={handleChange}
                    />
                  </CInputGroup>
                  <CInputGroup className='mb-3'>
                    <CInputGroupText>@</CInputGroupText>
                    <CFormInput
                      placeholder='ایمیل'
                      autoComplete='email'
                      name='email'
                      onChange={handleChange}
                    />
                  </CInputGroup>
                  <CInputGroup className='mb-5'>
                    <CInputGroupText>
                      <CIcon icon={cilLockLocked} />
                    </CInputGroupText>
                    <CFormInput
                      type='password'
                      name='password'
                      placeholder='رمز عبور'
                      autoComplete='new-password'
                      onChange={handleChange}
                    />
                  </CInputGroup>

                  <div className='d-grid'>
                    <CButton color='success' onClick={handleSubmit}>
                      ساحت اکانت
                    </CButton>
                  </div>
                </CForm>
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      </CContainer>
    </div>
  );
};

export default Register;
